.alert-dialog {
	position:fixed;
	width:500px;
	height:200px;
	top:50%;
	left:50%;
	margin-left:-250px;
	margin-top:-100px;
	display:block;
}

.alert-dialog-footer {
	position: absolute; 
	width:100%; 
	bottom: 0; 
	left:0; 
	padding:10px;
	& div {
		padding: 0 0 0 2px;
	}
	& div:first-child {
		padding: 0 2px 0 0;
	}

}

