.ufida-grid{
    display: block;
    position:relative;
    color:#444;
    background-color:#F9F9F9;
    zoom:1;
    font-family:Arial,Helvetica,sans-serif;
    font-weight:normal;
    font-size:16px;
    border-width:1px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-shadow:none;
    -webkit-box-shadow:none;
}

.ufida-grid-top{
    display: none;
    position: fixed;
    width:100%;
    height:100%;
    left:0px;
    top:0px;
    z-index: 99999;
}

.ufida-grid table{
    width: 100%;
    margin:0;
    max-width:none;
    border-collapse:separate;
    border-spacing:0;
    empty-cells:show;
    border-width:0;
    outline:0;
    table-layout: fixed;
}

.ufida-grid td{
    padding:.729em 1.186em .729em 1em;
    border-style:solid;
    border-color:#e5e5e5;
    border-width:0 0 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    line-height:1.6em;
    vertical-align:middle;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    height:3em;
}
/*
header区域css begin
*/
.ufida-grid-grid{
    
}
.ufida-grid-header{
    border-bottom-style: solid;
    border-width: 0 0 1px 0;
    border-color: #e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background-image: none;
    background-position: 50% 50%;
    background-repeat: repeat;
}

.ufida-grid-header-wrap{
	position:relative;
	width:100%;
	overflow:hidden;
	zoom:1;
}

.ufida-grid-header-multi-select{
    padding: .529em 0 0 .9em;
    position:relative;
    float: left;
    height:2.1875em;
    border-style: solid;
    border-color: #e5e5e5;
    border-width:0 1px 0 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-header-num{
    position:relative;
    float: left;
    height:2.1875em;
    border-style: solid;
    border-color: #e5e5e5;
    border-width:0 1px 0 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-header-th{
    position: relative;
    vertical-align:bottom;
//  padding:.8em .6em .8em .6em;
    height:2.1875em;
    font-weight:normal;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    text-align:left;
    border-style:solid;
    border-color:#e5e5e5;
    border-width:0 0 0 1px;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background-image:none;
    background-position:50% 50%;
    
}
.ufida-grid-header-th-first{
    border-left-width:0;
}
.ufida-grid-header-link{
    display:block;
    padding-left:1.3em;
    overflow:hidden;
    text-overflow:ellipsis;
    color:#a8a8a8;
    font-size: .8125em;
    height:2.69em;
    line-height:2.9em;
}

.ufida-grid-header-link:hover{
    color:#a8a8a8;
}
.ufida-grid-header-link:visited{
    color:#a8a8a8;
}

.ufida-grid-header-columnmenu{
    position:absolute;
    padding:.3em .7em .5em .6em;
    font-size:1.385em;
    top:0px;
    right: 0px;
    color:#a8a8a8;
    height:100%;
}
/*拖动*/
.ufida-grid-header-resize-handle{
    position:absolute;
    cursor:col-resize;
    z-index:2;
    width:.6em;
    height: 2.6875em;
    display: block;
    top: 0px;
}
/*交换*/
.ufida-grid-header-drag-clue{
    overflow:hidden;
    border-radius:2px;
    background-color:#3f51b5;
    position:absolute;
    z-index:10003;
    border-style:solid;
    border-width:1px;
    font-size:.9em;
    white-space:nowrap;
    cursor:default;
    color: #fff;
}

.ufida-grid-header-drag-status{
    width: 16px;
    height: 16px;
}


.ufida-grid-header-swap-tip-span{
    height:5px;
    position:absolute;
    z-index:10003;
    cursor:default;
    display: none;
}

/*排序*/
.ufida-grid-header-sort-span{
    width:16px;
    text-align: center;
}
/*
header区域css end
*/

/*
content区域css begin
*/

.ufida-grid-content{
    position:relative;
    width:100%;
    height:100%;
    overflow-x:scroll;
    overflow-y:scroll;
    zoom:1;
    background-color:#fff ;
}

.ufida-grid-content-left{
    position:absolute;
}

.ufida-grid-content-multiSelect{
    padding:.729em 0 0 .9em;
    border-style: solid;
    border-color: #e5e5e5;
    border-width: 0 1px 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    line-height:1.6em;
    height:3em;
    position:relative;
    float:left;
}

.ufida-grid-content-num{
    padding:0.729em 1.186em 0.729em 1em;
    border-style: solid;
    border-color: #e5e5e5;
    border-width: 0 1px 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    line-height:1.6em;
    height:3em;
    position:relative;
    float:left;
}

/*
content区域css end
*/
/*
分页区域css begin
*/
.ufida-grid-pagebar{
    position:relative;
    width: 100%;
}
.ufida-grid-pagebar-div{
    position: relative;
    padding:1em 0.9em;
    float:left;
    color:#a8a8a8;
    font-size:1.2em;
    cursor: pointer;
}
/*
分页区域css end
*/
/*
columnMenu区域css begin
*/
.ufida-grid-column-menu{
    width:160px;
    display: none;
    overflow: hidden;
    position: absolute;
    z-index: 10002;
    -webkit-box-sizing:content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}
.ufida-grid-column-menu-ul{
    background-color: #fff;
    font-weight:500;
    background-image: none;
    padding: 0px;
    background-position: 50% 50%;
    border-width: 0px 0px 1px 0px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;

}

.ufida-grid-column-menu-li{
    display: block;
    height: 2.6em;
    border-width:1px 1px 0px 1px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-column-menu-div1{
    display: block;
    padding: .8em;
    line-height: 1.34em;
    font-size:.875em;
    position: relative;
    height: 100%;
}  

.ufida-grid-column-menu-span{
    position: absolute;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.ufida-grid-column-menu-div3{
    margin: .2em -.5em 0 0;
    right: 2em;
    position: absolute;
    font-size:.875em;
    filter:alpha(opacity=70); 
    -moz-opacity:0.7;
    -khtml-opacity:0.7; 
    opacity: .7;
}
/*
columnMenu区域css end
*/

/*
columnMenu columns区域css begin
*/
.ufida-grid-column-menu-columns{
    width:160px;
    margin-left:-2px;
    padding-left: 2px;
    padding-right:2px;
    padding-bottom: 4px;
    display: none;
    overflow: hidden;
    position: absolute;
    z-index: 10002;
    -webkit-box-sizing:content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}
.ufida-grid-column-menu-columns-ul{
    border-width: 1px 0px 1px 0px;
    border-style: solid;
    border-color:#e5e5e5;
    background-color: #fff;
    font-weight:500;
    background-image: none;
    padding: 0px;
    background-position: 50% 50%;
}

.ufida-grid-column-menu-columns-li{
    display: block;
    height: 2em;
    border-width:0px 1px 0px 1px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-column-menu-columns-div1{
    display: block;
    padding: .8em .8em 0px .8em;
    line-height: 1.34em;
    font-size:.875em;
    position: relative;
    height: 100%;
}  

.ufida-grid-column-menu-columns-div2{
    margin: 2px 8px 0px 0px;
    position: absolute;
    filter:alpha(opacity=70); 
    -moz-opacity:0.7;
    -khtml-opacity:0.7; 
    opacity: .7;
}   

.ufida-grid-column-menu-columns-span{
    position: absolute;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    left:35px;
}
/*
columnMenu columns区域css end
*/
/*
form展示区域css begin
*/
.ufida-grid-form{
    width:100%;
    height:100%;
    overflow-x: hidden;
    overflow-y: scroll; 
}
/*
form展示区域css end
*/


